$kirenTheme: rgb(21, 129, 21);

.App {
  text-align: center;
  color: $kirenTheme;}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite alternate 1s linear, App-logo-translate infinite alternate 5s linear;
  }
}
@function calculateCircumference($radius) {
  @return 2 * 3.14  * $radius
}

#chucknorris {
  margin-top: -10%;
  height: 10em;
  border-radius: 300px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: translate(-10vh, 0%) rotate(-30deg) ;
  }
  to {
    
    transform: translate(10vh, 0%) rotate(30deg) ;
  }



}